div[contenteditable] {
	outline: 1px solid transparent;
}
.cke_dialog_title {
    background: #fff!important;
    color: #2a2a2a!important;
    border-bottom: 1px solid #f2f2f2!important;
}

.cke_dialog_body label {
    color: #5b5c60!important;
}

.cke_dialog_footer {
    background: #fff!important;
    border-top: 1px solid #f2f2f2!important;
}

a.cke_dialog_ui_button {
    border: solid 1px #e5e5ea!important;
    background-color: #ffffff!important;
}

a.cke_dialog_ui_button_ok {
    border-radius: 4px!important;
    background-color: #386f91!important;
}

.cke_dialog_contents {

    border-top: 1px solid #f2f2f2!important;
}

a.cke_dialog_tab {
    border: 1px solid #f2f2f2!important;
}

input.cke_dialog_ui_input_text, input.cke_dialog_ui_input_password, input.cke_dialog_ui_input_tel, textarea.cke_dialog_ui_input_textarea {
    border: 1px solid #f2f2f2!important;
}

.cke_textarea_inline {
	padding: 10px 16px;
	height: 200px;
	overflow: auto;
	-webkit-appearance: textfield;
}